import axios from '@/utils/uniAxios.js'
export default {
	data() {
		return {
			focusFalse:false,
			reason:'请选择',
			show:false,
			value:'',
			phone:'',
			form: {
				collect: [{
					time: '2019-04-06 11:37',
					shopName: '麦田圈官网旗舰店',
					state: 5,

					goodsList: [{
						title: '香辣牛肉干',
						price: 88.88,
						image: '../static/goods.png',
						number: 1,
						attr: '规格 10*200g'
					}]
				}],
				goodsObj: {
					remarks: '香辣味的',
					allPay: '120',
					jifen: '100',
					preferential: '20.00',
					total: '880'
				},
			},
			statuss:'',
			show:false,
			marginBottom:'',
			fileList: [],
			columns: ['七天无理由退换', '商品损坏', '商品过期', '物流', '其他'],
			orderId:'',
			returnText:''
		}
	},
	onLoad(option){
		this.orderId = option.id;
		this.refundList()
	},
	methods: {
		textareaClick(){
			console.log(777)
			this.focusFalse = true;
			console.log(this.focusFalse)
			// this.$refs.uploadTexteare.focus()
		},
		refundList(){
			axios.post('/returnApply/info',{id:this.orderId}).then(res=>{
				if(res.data.code==200){
					this.form = res.data.data;
				}
			})
		},
		consult(){
			uni.navigateTo({
				url:'consult'
			})
		},
		afterRead(event) {
			this.focusFalse=  false
		      const { file } = event.detail;
			  let _this = this;
		      // 当设置 mutiple 为 true 是 file 是一个数组，mutiple 默认为 false，file 是一个对象
		      uni.uploadFile({
		        url: 'https://mini.cropcircle.com.cn/aliyun/oss/uploadimage', //仅为示例，非真实的接口地址
		        filePath: file.path,
		        name: 'file',
		        formData: { 'user': 'test' },
		        success (res){
					let url = JSON.parse(res.data).data.url;
		          // 上传完成需要更新fileList
		          _this.fileList.push({ ...file, url:url});
		        }
		      });
		    },
		deleteImg(ev){
			this.fileList.splice(ev.detail.index,1)
		},
		onCancel(){
			this.show = false
		},
		cellClick(){
			this.show = true
		},
		onConfirm(event){
			this.show = false;
			const { picker, value, index } = event.detail;
			this.form.reason = value;
		},
		save(){
			let obj= {
				companyAddressId:this.form.companyAddressId,
				description:this.returnText,
				orderId: this.form.id,
				proofPics:'',
				reason:this.form.reason,
				returnPhone: this.phone
			};
			if(!obj.description){
				this.$api.msg('请填写退货描述');
				return;
			}else if(!obj.reason){
				this.$api.msg('请选择退货原因');
				return;
			}else if(!obj.returnPhone){
				this.$api.msg('请填写手机号');
				return;
			}
			axios.post('/returnApply/create',obj).then(res=>{
				if(res.data.code==200){
					this.$api.msg('申请退款成功');
					uni.redirectTo({
						url: 'aftersale'
					});
				}
			})
		}
	}
}
